.burger-menu {
  position: absolute;
  top: 0;
  background: rgb(250, 19, 142);
  width: 300px;
  height: 100%;
  border: 1px solid rgb(252, 19, 143);
  box-sizing: border-box;
  transition: 0.3s ease-in-out;
  // transform: translateX(-100%);
  // opacity: 0;
  transform: translateY(-100%);

  &.active {
    // transform: translateX(0);
    // opacity: 1;
    transform: translateY(0);
  }

  .button-close {
    position: absolute;
    top: 36px;
    transform: translateY(-50%);
    left: 19px;
    width: 40px;
    height: 40px;
    object-fit: none;
    cursor: pointer;
  }
  .burger{
    &-list {
      display: flex;
      flex-direction: column;
      padding: 75px 0 0 0px;
      list-style: none;
    }
    &-item {
      border: 1px solid rgb(248, 187, 197);
  
      &:last-child {
        border-top: none;
      }
    }
    &-link {
      display: block;
      padding: 15px;
      text-decoration: none;
      color: #fff;
  
      &.active {
        background: #f3bbc582;
      }
      &:hover {
        background: rgb(221, 159, 221);
        text-decoration: underline;
      }
    }
  }
}
